* {
  box-sizing: border-box;
}

:root {
  --turquoise: #0cc;
}

html {
  font-size: 16px;
  line-height: 1.5;
}

body {
  color: #f8f9fa;
  background: #343a40;
  grid-template: "header" minmax(48px, max-content)
                 "main"
                 "footer" 80px
                 / 1fr;
  width: 100vw;
  height: 100vh;
  margin: 0;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  font-weight: 200;
  display: grid;
}

@media (width >= 600px) {
  body {
    grid-template: 48px auto / 1fr;
  }
}

header {
  background: #fbc403;
  border-bottom: 1px solid #fbc403;
  align-items: flex-end;
  padding: 10px 20px;
  display: flex;
}

@media (width >= 600px) {
  header {
    grid-template: ". navbar navbar"
    / 1fr minmax(auto, 900px) 1fr;
    padding: 10px 0;
    display: grid;
  }
}

a {
  color: #f8f9fa;
}

main {
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
  display: flex;
  overflow-y: scroll;
}

@media (width >= 600px) {
  main {
    flex-wrap: unset;
    display: grid;
  }
}

footer {
  border-top: 1px solid #fbc403;
  grid-area: footer;
}

@media (width >= 600px) {
  footer {
    display: none;
  }
}

article {
  opacity: 1;
  width: 100%;
  margin: 80px 0;
  transition: opacity 1.5s, transform 1.5s;
}

@media (width >= 600px) {
  article {
    max-width: 900px;
  }
}

article:first-of-type {
  margin-top: 0;
}

@media (width >= 600px) {
  article:first-of-type {
    margin-top: 40px;
  }
}

section {
  margin-bottom: 40px;
}

section:first-of-type {
  margin-bottom: 0;
}

@media (width >= 600px) {
  section:first-of-type {
    margin-bottom: 40px;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 200;
  line-height: 1.2;
}

h1 {
  font-size: 3rem;
}

@media (width >= 600px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.5rem;
}

strong {
  font-weight: 900;
}

mark {
  color: #0a0a0a;
  background: #fbc403;
  padding: 6px;
}

.fade-out {
  opacity: 0;
  transform: translateY(40px);
}

img {
  max-width: 100%;
}

hr {
  max-width: 66%;
  margin-top: 40px;
  margin-bottom: 40px;
}

.footer-nav {
  grid-gap: 1px;
  grid-template: 1fr / repeat(3, 1fr);
  height: 100%;
  display: grid;
}

.footer-nav__item {
  background: #fbc403;
  justify-content: center;
  align-items: center;
  display: flex;
}

aside {
  grid-area: aside;
  justify-content: center;
  align-items: flex-end;
  width: auto;
  height: auto;
  padding: 80px 20px;
  display: flex;
}

@media (width <= 600px) {
  aside {
    display: none;
  }
}

.aside-nav__item {
  background: #fbc403;
  border: 1px solid #343a40;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 60px;
  margin-top: 10px;
  display: flex;
}

.subline {
  margin: 10px 0;
  font-size: 2rem;
  display: block;
}

.img-rounded {
  border-radius: 50%;
}

.img-profile {
  width: 100%;
  max-width: 300px;
}

.img-centered {
  align-self: center;
}

.img-reference {
  background: #fff;
  padding: 10px;
}

.img-portrait {
  border-radius: 50%;
  align-self: center;
  width: 100%;
  max-width: 300px;
  margin: 10px 0;
}

.img-portrait--left {
  border-radius: 50%;
  align-self: center;
  width: 100%;
  max-width: 275px;
  margin: 20px 0;
}

@media (width >= 900px) {
  .img-portrait--left {
    display: none;
  }
}

.img-portrait--right {
  border-radius: 50%;
  align-self: center;
  width: 100%;
  max-width: 300px;
  margin: 20px 0;
}

@media (width <= 900px) {
  .img-portrait--right {
    display: none;
  }
}

.badge {
  color: #0a0a0a;
  background: #fbc403;
  border-radius: 6px;
  margin-bottom: 4px;
  padding: 2px 10px 4px;
  font-size: 1.2rem;
  display: inline-block;
}

.badge--border {
  color: #fbc403;
  border: 1px solid #fbc403;
  border-radius: 6px;
  margin-bottom: 4px;
  padding: 2px 10px 4px;
  font-size: 1.2rem;
  display: inline-block;
}

.badge--border:hover {
  color: #343a40;
  background: #fbc403;
}

.large-unicode {
  text-align: center;
  font-size: 6rem;
}

.link-with-icon {
  color: #343a40;
  background: #f8f9fa;
  border-radius: 6px;
  grid-template: auto / auto auto;
  align-items: center;
  width: max-content;
  margin: 10px 0;
  transition: all 1s ease-in;
  display: grid;
}

.link-with-icon__icon {
  background: #fbc403;
  border-radius: 6px;
  padding: 6px 10px;
}

.link-with-icon__link {
  margin: 0 10px;
}

.link-with-icon__link--hidden {
  width: 0;
  margin: 0 0 0 -50px;
  transition: all 1s ease-in-out;
  display: none;
}

.link-with-icon:hover .link-with-icon__link--hidden {
  width: auto;
  margin: 0 10px;
  display: block;
}

.fas, .far {
  color: #fbc403;
}

.contact {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media (width >= 900px) {
  .contact {
    flex-direction: row;
    align-items: flex-start;
  }

  .contact a {
    margin-right: 24px;
  }
}

.cta {
  text-align: center;
  cursor: pointer;
}

@media (width >= 900px) {
  .cta {
    text-align: left;
  }
}

.cta__badge {
  color: #343a40;
  background: #fbc403;
  border-radius: 6px;
  align-self: center;
  margin-top: 0;
  margin-bottom: 4px;
  padding: 2px 10px 4px;
  font-size: 1.6rem;
  font-weight: 900;
  display: inline-block;
}

.skills {
  text-align: center;
}

@media (width >= 900px) {
  .skills {
    text-align: left;
  }
}

.navbar {
  grid-template: auto auto / repeat(2, auto);
  grid-area: navbar;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  display: grid;
}

@media (width >= 600px) {
  .navbar {
    grid-gap: 10px;
    grid-template: auto / repeat(7, auto);
    justify-content: flex-start;
    padding: 0 20px;
    display: grid;
  }
}

.navbar__brandname, .navbar__item {
  color: #0a0a0a;
  text-decoration: none;
}

.navbar__brandname {
  vertical-align: sub;
  font-size: 1.2rem;
}

@media (width <= 600px) {
  .navbar__item {
    display: none;
  }
}

.navbar__toggler {
  color: #0a0a0a;
  cursor: pointer;
  font-size: 1.4rem;
}

@media (width >= 600px) {
  .navbar__toggler {
    display: none;
  }
}

.navbar__dropdown {
  margin-bottom: 10px;
  padding-left: 20px;
  display: none;
}

@media (width >= 600px) {
  .navbar__dropdown {
    display: none;
  }
}

.navbar__dropdown__item {
  margin-bottom: 10px;
  list-style-type: none;
}

@media (width <= 600px) {
  .navbar:hover .navbar__dropdown, .navbar:active .navbar__dropdown {
    display: block;
  }
}

.navbar__dropdown__link {
  color: #0a0a0a;
  text-decoration: none;
}

